body{
  margin: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.788);
  background-image: url(../images/planet.jpg) no-repeat center center;
  height: 400px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.hidden{
  display:none
}
canvas{
  top:338px;
  left:650px;
  transform:translate(50%, 50%);
  width: 600px;
  height: 550px;
  font-size: 22px;
  color:rgb(255, 0, 230);
  background:linear-gradient(45deg, transparent 5%);
  border-radius: 1vh;
  box-shadow: -10px 15px 20px rgba(0, 0, 0, 0.41);
}

@media (max-width: 1900px) {
  canvas {
    border: 5px solid white;
    margin-top: -15vh;
  }
}


.canvasText{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 25px;
  margin-top: -9vh;
}

.controls{
  font-family:cursive;
  color:rgb(255, 255, 255);
  background-color:#ff0;
  margin-right: 250px;
  position:absolute;
  z-index:10;
  top:100;
  left:50%;
  transform:translateX(-50%)
}
/* mysite */
#gravity{
  border:2px solid #00e6f6;
  background-color: peachpuff;
  color: #919191;
  position:absolute;
  z-index:10;
  display:flex;
  justify-content: center;
  align-items:center;
  width:300px;
  height:48px;
}

#gravityControls{
  z-index:1;
  color: white;
  position:absolute;
  display:flex;
  flex-direction: column;
  justify-content:center;
  align-items:center;
  width:300px;
  height: auto;
  background: rgba(255, 255, 255, 0.061);
  backdrop-filter: blur(5px);
  background:linear-gradient(45deg, transparent 5%);
  font-family: 'Poppins', sans-serif;
  box-shadow: -10px 15px 20px rgba(0, 0, 0, 0.41);
  border-radius: 1vh;
  transition: margin-top 0.3s, height 0.3s;
  overflow: hidden;
}

.gravityControlsText{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 7vh;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

#gravity:hover #gravityControls {
  margin-top: 252px;
  height: 300px;
}

#hud{
  // border:2px solid #ff013c;
  background-image:url("https://img.itch.zone/aW1hZ2UvODIxMDAzLzUxOTc3NTYucG5n/original/gfXuIr.png");
  position:absolute;
  margin-top: 155px;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  width: 600px;
  height: 450px;
  font-size: 22px;
  color: aqua;
  // background: rgba(255, 255, 255, 0.061);
  background: transparent;
  backdrop-filter: blur(5px);
  background:linear-gradient(45deg, transparent 5%);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  z-index: 1;
  flex-direction: column;
}

#linkedin{
  background-color:#000
}

.score{
  display:flex;
  justify-content:center;
  margin-top: 200px;
  height: 50px;
}

#output{
  color:#00E6F6;
  width: 50px;
  // display: flex;
  // justify-content: center;
  margin-right: 580px;
  margin-top: 100px;
  z-index: 1;
}

.buttonContainer{
  display: flex;
  z-index: 10;
  height: 25px;
  width: 5px;
  position: fixed;
  right: 10%;
  background:url(./src/audio/2.mp3) no-repeat center center/cover;
  flex-direction: row;
  margin-right: 12vh;
}
.btn button{
  // width:50px;
  border: none;
  height:50px;
  background-color: transparent;
  color:#00e6f6;
  margin-bottom: 50px;
  cursor: pointer;
  // margin: 150px;
  // right:80px
}
.win{
  position:absolute;
  top:25%;
  right:49%;
  transform:translate(50%, 50%);
  width:150px;
  height:75px;
  color: transparent;
  background:linear-gradient(45deg, transparent 5%);
  z-index: 1;
}

#start{
  cursor: pointer;
  background-color: #00E6F6;
  color: white;
  width: 200px;
  height: 45px;
  z-index: 1;
  display: flex;
  // margin-top: -747px;
  // margin-left: 195px;
  justify-content: center;
  align-items: center;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  display: hidden;
}

#endGame{
  cursor: pointer;
  background-color: #00E6F6;
  color: #fff;
  width: 200px;
  height: 45px;
  // margin-top: 175px;
  // margin-top: -50px;
  // margin-left: 200px;
  // z-index: 1;
  // position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  // margin-top: 175px;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  display: hidden;
}

.play{
  border: none;
  height: 50px;
  width: 50px;
}
.pause{
  border: none;
  height: 50px;
  width: 50px;
}

.bodyC{
  border: 3px solid aqua;
}

.textInsideCanvas{
  font-family: 'Poppins', sans-serif;
  border-radius: 1vh;
  background-color: #0000004a;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: -10px 15px 20px rgba(0, 0, 0, 0.41);
}

.instructionsCanvas{
  padding: 10px;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

.linkesToCode{
  height: auto;
  width: auto;
  display: flex;
}
